import React from 'react'
import './Blog.css'

class Blog extends React.Component{
	render(){
		return(
			<>
				<article class="br2 ba dark-gray b--black-10 mv4 center grow">
					  <img src={this.props.b.img} class="db w-100 br2 br--top" alt="wallpaper" />
					  <div class="pa2 ph3-ns pb3-ns">
					    <div class="dt w-100 mt1">
					      <div class="dtc">
					      	<a href={this.props.b.link} target="_blank" rel="noopener noreferrer">
					        	<h1 class="f5 f4-ns mv0">{this.props.b.title}</h1>
					        </a>
					      </div>
					    </div>
					  </div>
				</article>
				
			</>
		);
	}
}

export default Blog;